<template>
  <v-app>
    <v-app-bar app>
      <v-toolbar-title class="headline">
        <span>Failisüsteemi ühetasemeline indekseeritud paigutamine</span>
      </v-toolbar-title>
    </v-app-bar>

    <v-snackbar
      :timeout="4000"
      v-model="error"
      color="error"
      top
    >
      {{ errorMessage }}
    </v-snackbar>

    <v-content>
      <v-container fluid style="height: 100%" class="ma-0">
        <v-row dense>
          <v-col cols="8" dense>
            <v-row dense justify="center">
              <v-col>
                <v-card>
                  <v-row dense>
                    <v-col>
                      <v-card-title class="ml-2">Vali või sisesta järjend</v-card-title>

                    </v-col>

                  </v-row>

                  <v-divider class="ml-4 mr-4"></v-divider>
                  <v-radio-group v-model="radioGroup" style="width: 100%">
                    <v-row dense
                           class="ma-2"
                           v-for="n in predefinedPatterns"
                           :key="n.label"

                    >
                      <v-col cols="4">
                        <v-radio
                          :value="n.pattern"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            {{n.label}}
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="6">
                      <span>
                        {{n.pattern}}
                      </span>
                      </v-col>
                    </v-row>
                    <v-row dense
                           class="ma-2 mb-0"

                    >
                      <v-col cols="4">
                        <v-radio
                          value="custom"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            Enda oma
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="5">
                        <v-text-field class="ma-0 pa-0" v-model="customPattern"
                                      @click="radioGroup='custom'">

                        </v-text-field>
                      </v-col>
                      <v-spacer></v-spacer>
                      <v-col no-gutters class="pb-4 mr-4" style="flex-flow: 0; flex-shrink: 1">
                        <v-btn color="green" text @click="save" outlined>Käivita</v-btn>
                      </v-col>
                    </v-row>
                  </v-radio-group>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="3" dense>
            <v-row dense justify="center">
              <v-col>
                <v-card class="pa-2">
                  <v-row>
                    <v-card-title>
                      Arvutused:
                    </v-card-title>
                  </v-row>

                  <ul>
                    <li>
                      <v-row>
                        <v-col cols="12">
                          <span>Allesjäänud failidest on fragmenteerunud <u>{{ fragmentation ? parsePercent(fragmentation.fragmentedFiles) : "_" }}</u>.</span>
                        </v-col>
                      </v-row>

                    </li>
                    <li>
                      <v-row>
                        <v-col cols="12">
                          <span>Fragmenteerunud failidele kulub  <u>{{ fragmentation ? parsePercent(fragmentation.fragmentedData) : "_" }}</u> kasutatud ruumist.</span>
                        </v-col>
                      </v-row>
                    </li>
                  </ul>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
        </v-row>

        <v-row justify="center" dense>
          <v-col align="center" ref="canvasWrapper">
            <canvas ref="drawing"></canvas>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  import algorithms from './plugins/algorithms';

  export default {
    name: 'App',
    data: () => ({
      result: undefined,
      radioGroup: undefined,

      error: false,
      errorMessage: "",

      colors: ["#206F71",
        "#8BC9AE",
        "#FCEBA9",
        "#FCB06F",
        "#D94B50",
        "#D291BC",
        "#C1E7E3",
        "#FFDCF4",
        "#AFDAC1",
        "#B399D4",
        "#6B7B6E",
        "#4F86C6"],
      colorIndex: 0,

      algorithms: [{label: "Last-Fit", func: algorithms.algorithm}],

      customPattern: "",

      selectedAlgorithm: 0,

      fragmentation: undefined,

      predefinedPatterns: [
        {
          label: "Esimene",
          pattern: "A,2;B,3;A,-;C,4;D,5;B,-;E,15"
        },
        {
          label: "Teine",
          pattern: "X,5;G,3;K,10;G,-;J,15;X,-;B,1;A,2;H,4"
        },
        {
          label: "Kolmas",
          pattern: "D,3;H,7;S,12;A,8;J,3;T,6;H,-;X,2;V,16;X,-;Y,1;S,-;H,6"
        }
      ],

      labels: []
    }),

    methods: {
      parsePercent: function (val) {
        return parseFloat(val * 100).toFixed(0)+"%"
      },
      save: function () {
        this.error = false;
        try {
          let pattern;
          if (this.radioGroup === "custom") {
            pattern = this.customPattern;
          } else {
            pattern = this.radioGroup;
          }

          if (!pattern) {
            return;
          }

          this.result = this.algorithms[this.selectedAlgorithm].func(pattern);
          this.fragmentation = this.result.fragmentation;
          console.log(this.result);
        } catch (e) {
          this.error = true;
          this.errorMessage = e;
          return;
        }

          this.drawMemory(this.result);
        },

      drawMemory: function (processHistory) {
        let canvas = this.$refs["drawing"];
        let canvasWrapper = this.$refs["canvasWrapper"];
        canvas.width = Math.min(canvasWrapper.clientWidth - 24, 1400);


        const ctx = canvas.getContext("2d");

        let sideLen = Math.floor((canvas.width) / 55);
        ctx.fillStyle = "black";

        canvas.height = sideLen * (processHistory.memory.length + 3);

        ctx.font = "12px Arial";

        for (let j = 0; j < algorithms.maxSize; j++) {
          let x = sideLen * (j + 2.9);
          let y = sideLen * 1.8;
          ctx.fillText(j, x, y);
        }

        for (let i = 0; i < processHistory.memory.length; i++) {

          if (typeof processHistory.memory[i] === "string") {
            this.error = true;
            this.errorMessage = processHistory.memory[i];
            return;
          }

          let y1 = sideLen * (i + 2);

          ctx.font = "12px Arial";

          if (i > 0) {
            ctx.fillText("Samm " + (i), 0, y1 + sideLen / 1.3);
          }

          if (i < this.labels.length) {
            ctx.fillText(this.labels[i], sideLen * 3, y1 + sideLen / 1.3);
          }

          for (let j = 0; j < algorithms.maxSize; j++) {
            let x1 = sideLen * (j + 2.5) + 1;
            let x2 = sideLen + 1;
            let y2 = sideLen + 1;


            ctx.fillStyle = processHistory.memory[i][j] === undefined ? "white" : this.colors[processHistory.memory[i][j].charCodeAt(0) % this.colors.length];
            ctx.fillRect(x1, y1, x2, y2);

            ctx.beginPath();
            ctx.rect(x1, y1, x2, y2);
            ctx.stroke();

            ctx.fillStyle = "black";
            ctx.font = "10px Arial";
            ctx.fillText(processHistory.memory[i][j] === undefined ? "-" : processHistory.memory[i][j], x1 + sideLen / 2.4, y1 + sideLen / 1.5);
            // console.log(x1, y1, x2, y2);

          }
        }

      },

      select: function (n) {
        this.selectedAlgorithm = n - 1;
        this.save();
      }
    },

    computed: {},

    created: function () {
      this.radioGroup = this.predefinedPatterns[0].pattern;
    },

    watch: {
      radioGroup: function () {
        this.save();
      }
    }

  };
</script>

<style>
  .v-input__control {
    width: 100% !important;
  }
</style>
