const algorithms = {
  maxSize: 50,

  decodePattern: (pattern) => {
    let split = pattern.split(';');
    return split.map((value, index) => {
      let values = value.split(",");
      if (values[1] === "-") {
        return {
          type: "delete",
          label: values[0]
        };
      } else {
        return {
          type: "new",
          size: parseInt(values[1]),
          label: values[0]
        };
      }
    });
  },

  calculateFragmentation: (files) => {
    let fragmentedCount = 0;
    let fragmentedSize = 0;
    let totalSize = 0;
    let totalCount = 0;

    for (let fileName of Object.keys(files)) {
      let file = files[fileName];
      if (!file) {
        continue;
      }

      totalSize += file.size;
      totalCount++;

      if (file.fragmented) {
        fragmentedSize += file.size;
        fragmentedCount++;
      }
    }

    return {
      fragmentedFiles: fragmentedCount / totalCount,
      fragmentedData: fragmentedSize / totalSize
    }
  },

  algorithm: (pattern) => {
    let decoded = algorithms.decodePattern(pattern);
    let memorySnapshots = [];
    let memory = new Array(algorithms.maxSize);
    memorySnapshots.push([...memory])
    let files = {};

    for (let i = 0; i < decoded.length; i++) {
      let action = decoded[i];

      if (action.type === "delete") {
        files[action.label] = undefined;
        for (let j = 0; j < memory.length; j++) {
          if (memory[j] === action.label) {
            memory[j] = undefined;
          }
        }
      } else {
        let file = {size: action.size, fragmented: false};
        let allocatedCount = 0;
        for (let j = 0; j < memory.length; j++) {
          if (memory[j] === undefined) {
            memory[j] = action.label;
            allocatedCount++;
            if (allocatedCount === action.size) {
              break;
            }
          } else {
            if (allocatedCount > 0) {
              file.fragmented = true;
            }
          }
        }

        if (allocatedCount < action.size) {
          memorySnapshots.push("Fail " + action.label + " ei mahu kettale ära.")
          break;
        }

        files[action.label] = file;

      }
      memorySnapshots.push([...memory])
    }

    let fragmentation = algorithms.calculateFragmentation(files);

    return {fragmentation: fragmentation, memory: memorySnapshots}
  }


};

export default algorithms;
